@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;

.details-header-wrapper {
  margin-top: 6px;

  .search-input-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border: 1px solid pick-color('primary', 'fog');
    margin-right: 10px;
    @include border-radius(8px);

    .search-input {
      padding: 12px 20px;
      border: none;
      background-color: transparent;
      font-size: 14px;
    }
  }

  icon-button {
    span {
      line-height: 19px;
      font-size: 13px;
      height: 19px;
    }
  }
}
